<template>
  <div class="searchresult">
    <div class="search">
      <div class="im-talk">
        <svg
          t="1665056351788"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="3028"
          width="35"
          height="35"
          @click="$router.back()"
        >
          <path
            d="M608 736c-6.4 0-19.2 0-25.6-6.4l-192-192C384 524.8 384 499.2 390.4 486.4l192-192c12.8-12.8 32-12.8 44.8 0s12.8 32 0 44.8L460.8 512l166.4 166.4c12.8 12.8 12.8 32 0 44.8C627.2 736 614.4 736 608 736z"
            p-id="3029"
            fill="#adadad"
          ></path>
        </svg>
      </div>
      <div class="input" @click="showSearchSuggest = true">
        <img src="../assets/home-search.png" alt="" />
        <input type="text" :placeholder="$route.query.keyword" />
      </div>
      <div class="cart" @click="$router.push({ path: '/shopcart' })"></div>
    </div>
    <ul class="sortFilter">
      <li
        v-for="sortF in searchResult?.sortFilter"
        :key="sortF.sortKey"
        @click="sort = sortF.sortKey"
      >
        <p :class="sortF.sortKey === sort ? 'active' : ''">
          {{ sortF.title }}
        </p>
      </li>
      <li @click="showpriceFilter = !showpriceFilter">
        <p :class="showpriceFilter ? 'active' : ''">价格</p>
      </li>
    </ul>

    <ul class="doc">
      <li
        v-for="d in searchResult?.wall?.docs"
        :key="d.iid"
        @click="$router.push({ path: '/commodity', query: { id: d.iid } })"
      >
        <div class="img" v-lazy:background-image="d.img">
          <img src="../assets/searchresult-imgIcon.png" />
        </div>
        <div class="msg">
          <p>
            <span v-if="d.leftbottom_taglist.length">{{
              d.leftbottom_taglist[0].content
            }}</span
            >{{ d.title }}
          </p>
          <div class="num">
            <p>&yen;{{ d.price }}</p>
            <p>{{ d.cfav }}</p>
          </div>
        </div>
      </li>
    </ul>

    <SearchSuggest
      v-if="showSearchSuggest"
      @exit-searchsuggest="showSearchSuggest = $event"
      @noshow-searchsuggest="showSearchSuggest = $event"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import SearchSuggest from "@/components/SearchSuggest.vue";
export default {
  components: {
    SearchSuggest,
  },
  data() {
    return {
      sort: "pop",
      page: 1,
      offset: 30,
      showpriceFilter: false,
      q: this.$route.query.keyword,
      showSearchSuggest: false,
    };
  },
  created() {
    this.$store.dispatch("getSearchResult", {
      q: this.q,
      page: this.page,
      sort: this.sort,
      offset: this.offset,
    });
  },

  computed: {
    ...mapState(["searchResult"]),
  },
  watch: {
    sort: function (newSort) {
      this.$store.dispatch("getSearchResult", {
        q: this.q,
        page: this.page,
        sort: newSort,
        offset: this.offset,
      });
    },
    q:function(newq){
      this.$store.dispatch("getSearchResult", {
        q: newq,
        page: this.page,
        sort: this.sort,
        offset: this.offset,
      });
    },
    $route: function (to) {
      this.$store.dispatch("getSearchResult", {
        q: to.query.keyword,
        page: this.page,
        sort: this.sort,
        offset: this.offset,
      });
    },
  },
};
</script>

<style lang="less" scoped>
.searchresult {
  .search {
    display: flex;
    align-items: center;
    padding: 10rem 0;
    border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
    .im-talk {
      width: 50rem;
      height: 30rem;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .input {
      width: 265rem;
      height: 30rem;
      display: flex;
      align-items: center;
      background: #eeeeee;
      img {
        width: 15rem;
        height: 15rem;
        margin: 0 5rem;
      }
      input[type="text"] {
        width: calc(100% - 25rem);
        height: 30rem;
        border: none;
        outline: none;
        background: #eeeeee;
      }
    }
    .cart {
      width: 50rem;
      height: 30rem;
      background: url(../assets/classify-cart.png) no-repeat center;
      background-size: 20rem;
    }
  }
  ul.sortFilter {
    display: flex;
    border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
    li {
      width: 33.33%;
      height: 40rem;
      display: flex;
      justify-content: center;
      align-items: center;
      p {
        width: 100%;
        text-align: center;
        font-size: 14rem;
        border-left: 1rem solid rgba(0, 0, 0, 0.05);
        &.active {
          color: #ef4562;
        }
      }
    }
  }

  ul.doc {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    background: #efefef;
    li {
      width: 47%;
      margin: 7.5rem 0 0 7.5rem;
      .img {
        width: 100%;
        height: 235rem;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        border-radius: 5rem 5rem 0 0;
        position: relative;

        img {
          width: 30rem;
          height: 30rem;
          position: absolute;
          bottom: 10rem;
          right: 10rem;
        }
      }
      .msg {
        background: white;
        p {
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          padding: 7.5rem 9rem 2.5rem 5rem;
          span {
            background: #fafafa;
            color: #c2c2c2;
            padding: 0 3rem;
            margin-right: 3rem;
          }
        }
        div.num {
          width: 100%;
          display: flex;
          justify-content: space-between;
          padding: 0 9rem;
          p {
            &:nth-child(1) {
              color: #333;
              font-size: 15rem;
              font-weight: bold;
            }
            &:nth-child(2) {
              font-size: 12rem;
              color: #999999;
              padding-right: 15rem;
              margin-top: 5rem;
              background: url(../assets/shoppingmall-whitestart.png) center
                right no-repeat;
              background-size: auto 12rem;
            }
          }
        }
      }
    }
  }
}
</style>